/* Customize the label (the custom-radio) */
.custom-radio {
	display: block;
	position: relative;
	padding-left: 24px;
	cursor: pointer;
	font-size: $font-size-18;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

/* Hide the browser's default custom-radio button */
.custom-radio input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
}

/* Create a custom custom-radio button */
.checkmark {
	position: absolute;
	top: $spacing-4;
	left: 0;
	height: 16px;
	width: 16px;
	background-color: $black;
	border-radius: 50%;
	border: solid 1px $green;
}

//  /* On mouse-over, add a grey background color */
//  .custom-radio:hover input~.checkmark {
//  	background-color: #ccc;
//  }

/* When the custom-radio button is checked, add a blue background */
.custom-radio input:checked~.checkmark {
	background-color: $black;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
	content: "";
	position: absolute;
	display: none;
}

/* Show the indicator (dot/circle) when checked */
.custom-radio input:checked~.checkmark:after {
	display: block;
}

/* Style the indicator (dot/circle) */
.custom-radio .checkmark::after {
	top: 3px;
	left: 3px;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background: $green;
}
